<template>
  <ContentBase>
    <div class="row justify-content-md-center">
      <div class="col-6">
        <form @submit.prevent="register">
          <div class="input-group mb-3">
            <span class="input-group-text"></span>
            <input v-model="username" type="text" class="form-control" id="username" required>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text"></span>
            <input v-model="realname" type="text" class="form-control" required>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text"></span>
            <input v-model="email" type="email" class="form-control" required>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text"></span>
            <input v-model="phone" type="tel" class="form-control" required>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text"></span>
            <input v-model="password" type="password" class="form-control" id="password" required>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text"></span>
            <input v-model="confirmPassword" type="password" class="form-control" id="confirmPassword" required>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text"></span>
            <input v-model.number="age" type="number" class="form-control" id="age" required>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text"></span>
            <select v-model="gender" class="form-select" id="gender" required>
              <option value="male"></option>
              <option value="female"></option>
            </select>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text"></span>
            <select v-model="education" class="form-select" required>
              <option value=""></option>
              <option v-for="option in educationOptions" 
                      :key="option.value" 
                      :value="option.value">
                {{ option.label }}
              </option>
            </select>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text">/</span>
            <input v-model.number="hourlyRate" type="number" class="form-control" id="hourlyRate" required>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text"></span>
            <select v-model="selectedProvinceCode" class="form-select" required>
              <option value=""></option>
              <option v-for="(name, code) in provinces" :key="code" :value="code">
                {{ name }}
              </option>
            </select>
            <select v-model="selectedCityCode" class="form-select" required>
              <option value=""></option>
              <option v-for="(name, code) in citys" :key="code" :value="code">
                {{ name }}
              </option>
            </select>
            <select 
              v-if="Object.keys(countrys).length > 0" 
              v-model="selectedCountryCode" 
              class="form-select" 
              required
            >
              <option value=""></option>
              <option v-for="(name, code) in countrys" :key="code" :value="code">
                {{ name }}
              </option>
            </select>
          </div>

          <div class="input-group mb-3">
            <span class="input-group-text"></span>
            <textarea v-model="description" class="form-control" id="description" rows="3"></textarea>
          </div>

          <div class="error-message">{{ error_message }}</div>
          <button type="submit" class="btn btn-primary"></button>
        </form>

        <div class="mt-3 text-center">
          <router-link :to="{ name: 'login' }" class="btn btn-link">
            
          </router-link>
        </div>
      </div>
    </div>
  </ContentBase>
</template>

<script>
import ContentBase from "../components/ContentBase";
import { ref, watchEffect } from "vue";
import { useStore } from "vuex";
import router from "@/router/index";
import $ from 'jquery';

export default {
  name: "RegisterTeacherView",
  components: {
    ContentBase,
  },
  setup() {
    const store = useStore();
    let username = ref("");
    let realname = ref("");
    let password = ref("");
    let confirmPassword = ref("");
    let age = ref(18);  // 18
    let gender = ref("male");  // 
    let education = ref("");
    let hourlyRate = ref(100);  // 100/
    let description = ref("");
    let error_message = ref("");

    //  ref
    const selectedProvinceCode = ref('110000');
    const provinces = ref([]);

    const selectedCityCode = ref('110101');
    const citys = ref([]);
    
    const selectedCountryCode = ref('');
    const countrys = ref({});  // 

    let email = ref("");
    let phone = ref("");

    const get_provinces = () => {
      $.ajax({
        url: "http://47.109.76.15:8000/tool/provinces/",
        type: "get",
        success: (resp) => {
          provinces.value = resp;
        },
      });
    };

    const get_citys = (code) => {
      if (!code) {
        citys.value = {};
        selectedCityCode.value = '';
        return;
      }
      $.ajax({
        url: "http://47.109.76.15:8000/tool/cities/",
        type: "get",
        data: {
          'province_code': code
        },
        success: (resp) => {
          citys.value = resp;
          selectedCityCode.value = Object.keys(resp)[0];
        },
      });
    };

    const get_country = (code) => {
      if (!code) {
        countrys.value = {};
        selectedCountryCode.value = '';
        return;
      }
      $.ajax({
        url: "http://47.109.76.15:8000/tool/country/",
        type: "get",
        data: {
          'city_code': code
        },
        success: (resp) => {
          countrys.value = resp || {};  // 
          if (Object.keys(countrys.value).length > 0) {
            selectedCountryCode.value = Object.keys(resp)[0];
          } else {
            selectedCountryCode.value = '';
          }
        },
      });
    };

    // 
    watchEffect(() => {
      if (selectedProvinceCode.value) {
        get_citys(selectedProvinceCode.value);
      } else {
        citys.value = {};
        countrys.value = {};
        selectedCityCode.value = '';
        selectedCountryCode.value = '';
      }
    });

    // 
    watchEffect(() => {
      if (selectedCityCode.value) {
        get_country(selectedCityCode.value);
      } else {
        countrys.value = {};
        selectedCountryCode.value = '';
      }
    });

    // 
    get_provinces();

    const validateForm = () => {
      if (!username.value) {
        error_message.value = "";
        return false;
      }
      if (!realname.value) {
        error_message.value = "";
        return false;
      }
      if (!email.value) {
        error_message.value = "";
        return false;
      }
      if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email.value)) {
        error_message.value = "";
        return false;
      }
      if (!phone.value) {
        error_message.value = "";
        return false;
      }
      if (!/^1[3-9]\d{9}$/.test(phone.value)) {
        error_message.value = "";
        return false;
      }
      if (!password.value) {
        error_message.value = "";
        return false;
      }
      if (password.value !== confirmPassword.value) {
        error_message.value = "";
        return false;
      }
      if (!age.value) {
        error_message.value = "";
        return false;
      }
      if (!gender.value) {
        error_message.value = "";
        return false;
      }
      if (!education.value) {
        error_message.value = "";
        return false;
      }
      if (!hourlyRate.value) {
        error_message.value = "";
        return false;
      }
      if (!selectedProvinceCode.value || !selectedCityCode.value) {
        error_message.value = "";
        return false;
      }
      if (Object.keys(countrys.value).length > 0 && !selectedCountryCode.value) {
        error_message.value = "";
        return false;
      }
      return true;
    };

    // 
    const getEducationValue = (education) => {
      const educationMap = {
        'primary': 0,    // 
        'junior': 1,     // 
        'senior': 2,     // 
        'bachelor': 3,   // 
        'master': 4,     // 
        'doctor': 5      // 
      };
      return educationMap[education] || 3;  // 
    };

    const register = () => {
      error_message.value = "";
      
      if (!validateForm()) {
        return;
      }

      // 
      const registerData = {
        username: username.value,
        password: password.value,
        password_confirm: confirmPassword.value,
        nickname: realname.value,
        age: age.value.toString(),
        gender: gender.value === 'male' ? '0' : '1',
        eq: getEducationValue(education.value).toString(),
        status: '0',
        email: email.value,
        phonenumber: phone.value,
        province: provinces.value[selectedProvinceCode.value],  // 
        city: citys.value[selectedCityCode.value],             // 
        county: Object.keys(countrys.value).length > 0 
               ? countrys.value[selectedCountryCode.value]      // 
               : citys.value[selectedCityCode.value],          // 
        area: description.value || '',
        hprice: hourlyRate.value.toString(),
      };

      console.log('Sending registration data:', registerData);  // 

      $.ajax({
        url: "http://47.109.76.15:8000/teacher/register/",
        type: "POST",
        data: registerData,
        success(resp) {
          console.log('Registration response:', resp);
          if (resp.result === "add teacher success") {
            store.dispatch("login", {
              username: username.value,
              password: password.value,
              loginType: 'teacher',
              success() {
                router.push({ name: "home" });
              },
              error() {
                error_message.value = "";
              },
            });
          } else {
            error_message.value = resp.result;
          }
        },
        error(xhr, status, error) {
          console.log('Registration error:', error);
          error_message.value = "";
        }
      });
    };

    // 
    const educationOptions = [
      { value: 'primary', label: '' },
      { value: 'junior', label: '' },
      { value: 'senior', label: '' },
      { value: 'bachelor', label: '' },
      { value: 'master', label: '' },
      { value: 'doctor', label: '' }
    ];

    return {
      username,
      realname,
      password,
      confirmPassword,
      age,
      gender,
      education,
      hourlyRate,
      description,
      error_message,
      register,
      selectedProvinceCode,
      selectedCityCode,
      selectedCountryCode,
      provinces,
      citys,
      countrys,
      email,
      phone,
      educationOptions,  // 
    };
  },
};
</script>

<style scoped>
button[type="submit"] {
  width: 100%;
}

.error-message {
  color: red;
}

.input-group-text {
  width: 120px;  /*  */
  justify-content: center;  /*  */
}

textarea {
  resize: vertical;  /*  */
}

.input-group select {
  flex: 1;  /* select */
  margin: 0 2px;  /*  */
}

.input-group select:first-of-type {
  margin-left: 0;  /* select */
}

.input-group select:last-of-type {
  margin-right: 0;  /* select */
}

.bi-arrow-left {
  margin-right: 5px;
}
</style> 